<template>
  <div id="user">
      <div class="show-div">
        <el-table :data="userList" style="width: 100%">
          <el-table-column  prop="username"  label=""  width="100"></el-table-column>
          <el-table-column  prop="password"  label=""  width="100"></el-table-column>
          <el-table-column  prop="email" label="" width="200"></el-table-column>
          <el-table-column  prop="regTime" label="" width="200"></el-table-column>
          <el-table-column  prop="videoNum" label="" width="100"></el-table-column>
          <el-table-column  prop="status" label="" width="70"></el-table-column>
          <el-table-column  label="" width="200"> 
            <template slot-scope="scope">
              <el-button @click="handleDelUser(scope.row)" type="danger" size="small" plain></el-button>
              <el-button @click="handleModPass(scope.row)" type="primary" size="small" plain></el-button>

            </template>
          </el-table-column>
        </el-table>
        <div class="page-div">
          <el-pagination background layout="prev, pager, next" 
                         :current-page="page.cur" :page-size="page.sze" :page-count="page.count"
                         @size-change="handleSizeChange"
                         @current-change="handleCurrentChange"
                         @prev-click="handlePreClick"
                         @next-click="handleClick">
         </el-pagination>
        </div>
      </div>
  </div>       
</template>    

<script>
import axios from 'axios'

  export default {
    name: "UserList",  
    data() {    
        return {
          page: {
            cur: 1,
            Size: 6,
            count: 20
          },
          userList: []
      }
    },
    created() {
      // 
      console.log('')
      this.showUsersByPage(1)
      console.log('')
    },
    methods: {
      showUsersByPage(curPage){
        console.log('---curPage ' + curPage)
        axios({
          method: 'get',
          url: '/showUsersByPage',
          params: {
            page: curPage
          }
        }).then(res => {
          res = res.data
          if(res.status == '1'){
            this.userList = res.userList
            this.page.count = res.pageCount
            console.log(this.userList)
          }
        })
      },
      openSucess(Str) {
        this.$message({
          message: str,
          type: 'success'
        })
      },
      openFail(str) {
        this.$message.error(str);
      },
      openCancle(str) {
        this.$message({
            type: 'info',
            message: str
          }); 
      },
      handleDelUser(row) {
        let username = row.username
        console.log('username: '+ row.username)
        this.$confirm(''+username+' ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // 
          this.delUser(username)
        }).catch(() => {
          this.openCancle('')
        })
      },
      delUser(username){
        axios({
          method: 'get',
          url: '/delUser',
          params: {
            username: username,
            page: this.page.cur
          }
        }).then(res => {
          res = res.data
          // 
          if(res.status == '1'){
            this.userList = res.userList
            this.page.count = res.pageCount
            this.openSucess('')
            console.log(this.userList)
          }else{
            this.openFail('')
          }
        })
      },
      handleModPass(row) {
        let that = this
        let username = row.username
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /\S/,
          inputErrorMessage: ''
        }).then(({ value }) => {
           console.log('' + value)
           axios({
             method: 'get',
             url: '/modPass',
             params: {
               username: username,
               newPass: value
             }
           }).then(res => {
              res = res.data 
              // 
              if(res.status == '1'){
                // 
                this.$message({
                type: 'success',
                message: ' ' + value
                });
                // 
                this.showUsersByPage(this.page.cur)
              }else{
                // 
                this.$message.error('')
              }
           })
        }).catch(() => {
          // 
          this.$message({
            type: 'info',
            message: ''
          });       
        });
      },
      
      // 
      handleSizeChange(size) {

      },
      handleCurrentChange(current) {
        this.page.cur = current
        console.log(current)
        this.showUsersByPage(current)
      },
      handlePreClick() {
        console.log(this.page.cur)
      },
      handleClick() {
        console.log(this.page.cur)
      }
      
    }
}

</script>
               
<style scoped>
.page-div{
  margin-top: 10px;
}
.del-btn{
  color: #d4237a;
}
.show-div{
  width: 1000px;
}
</style>
